}

:root {
  --border-radius: 5px;
  --light-color: #fff;
  --dark-color: #000;
  --gray-color: #545454;
  --modal-gray-color: #8c8c8c;
  --orange-color: #ff6b01;
  --ganre-orange-color: #ff6b08;
  --color-error: #ff001b;
  --shadow: drop-shadow(0px 8px 43px rgba(255, 107, 1, .6));
  --animationTransition: 250ms cubic-bezier(.4, 0, .2, 1);
  --font-family: "Roboto", sans-serif;
  --font-weight-normal: 400;
  --font-weight-media: 500;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

.lazyload, .lazyloading {
  opacity: 0;
  transition: all 1s;
  transform: translateX(-100%);
}

.lazyloaded {
  opacity: 1;
  transition: transform 1s, opacity 1s;
  transform: translateY(0);
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

body {
  font-family: var(--font-family), Arial, Helvetica, sans-serif;
  background: #abbaab;
  background: -webkit-linear-gradient(to right, #fff, #abbaab);
  background: linear-gradient(to right, #fff, #abbaab) center / cover no-repeat fixed;
  margin: 0;
  font-size: 14px;
}

body.stop-fon {
  overflow: hidden;
}

.scroll-btn-up {
  position: relative;
}

.scroll-btn {
  z-index: 15;
  height: 45px;
  background-color: var(--orange-color);
  transition: var(--animationTransition);
  opacity: .6;
  border-radius: 50%;
  padding: 10px;
  display: none;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.scroll-btn:hover {
  opacity: .8;
  transform: scale(1.1);
}

.scroll-btn__svg {
  fill: var(--gray-color);
}

.scroll-btn.shown {
  display: block;
}

.btn-form {
  cursor: pointer;
  width: 80px;
  height: 30px;
  outline: 2px solid var(--gray-color);
  color: var(--orange-color);
  border-radius: var(--border-radius);
  transition: var(--animationTransition);
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 5px;
  font-size: 10px;
}

.btn-form:hover, .btn-form:focus {
  background-color: var(--orange-color);
  outline: 2px solid var(--orange-color);
  color: var(--light-color);
}

.modal {
  max-width: 700px;
  background-color: #fff;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.img-container {
  max-width: 50%;
}

.modal-img {
  width: 100%;
  cursor: pointer;
  border-radius: 10px 0 0 10px;
}

.modal-wrapper {
  display: flex;
}

.modal-info {
  width: 50%;
  flex-direction: column;
  row-gap: 20px;
  padding: 10px;
  display: flex;
}

.skiptranslate {
  height: 0;
  display: none;
}

.language__img {
  cursor: pointer;
}

.cont-lang {
  position: relative;
}

.wrapper-language {
  z-index: 50;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  width: 150px;
  color: var(--orange-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: 1px solid #000;
  margin-left: 50px;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
  transition: transform .3s;
  position: relative;
}

.wrapper-language:hover, .wrapper-language:focus {
  transform: scale(1.1);
}

.language {
  width: 100%;
  pointer-events: none;
  color: gold;
  text-shadow: 0 0 1px #06e6f6;
  background-color: rgba(13, 13, 13, .3);
  border-radius: 10px;
  padding: 5px;
  transition: all .4s;
  position: absolute;
  top: 100%;
  left: 0;
  transform: perspective(600px)translate(0, -50%)rotateX(45deg)scale(0);
}

.language.shows {
  pointer-events: painted;
  transform: perspective(600px)translate(0)rotateX(0deg)scale(1);
}

.item-language {
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(9, 137, 235, .7);
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
  transition: transform .3s;
  display: flex;
}

.item-language:hover {
  transform: scale(1.02);
}

.item-language:not(:last-child) {
  margin-bottom: 5px;
}

